<template>
	<view>
		<uni-nav-bar class="nav-style" left-icon="back" right-icon="loop" :title="patient.name" color="#ffffff" background-color="#51D3C7" @clickLeft="toPatientList" @clickRight="reflesh"></uni-nav-bar>
		<view class="content">
			<form>
				<uni-segmented-control class="segment" :current="segCtrlSelected" :values="segCtrlItems" @clickItem="onClickItem" styleType="text" activeColor="#51D3C7"></uni-segmented-control>
				<view v-show="segCtrlSelected === 0">
					<view class="form-item">
						<view class="form-prefix-space">
							<text>上机前病情:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space2">
							<radio-group name="beforeCondition">
								<label  v-for="item in info.status" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
						<view class="form-cnt form-mid-space" v-show="statusInput">
							<input class="form-mid-space" value="啊啊啊啊啊啊啊啊啊啊" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>治疗时间:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="healHour" type="text" placeholder=""/>
							<text>h</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input type="text" :value="healMin" placeholder=""/>
							<text>min</text>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>净化次数:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="dialysisInfo.clean_up_times" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>治疗方式:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<picker mode="selector" :range="treatmentRange"  @change="setTreatment">
							<input class="form-mid-space" :value="dialysisInfo.treatmentitems" type="text" placeholder=""/>
							</picker>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>净化器类型:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<picker mode="selector" :range="fliterRange"  @change="setFliter">
							<input class="form-mid-space"  :value="dialysisInfo.filter" type="text" placeholder=""/>
							</picker>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>血管通路:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="dialysisInfo.allvascularaccess" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>穿刺:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="puncture">
								<label  v-for="item in info.puncture" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>方向:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="puncture">
								<label  v-for="item in info.direction" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>体温℃:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="dialysisInfo.before_temperature" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>脉搏(次/min):</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="dialysisInfo.before_bp" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>呼吸(次/min):</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space"  :value="dialysisInfo.br" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>抗凝方式:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<picker mode="selector" disabled="true" :range="bloodmethodRange"  @change="setBloodmethod">
								<input class="form-mid-space" disabled="true" :value="antiFreeze" type="text" placeholder=""/>
							</picker>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>首剂:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" disabled="true" :value="dialysisInfo.first_drug_dosage" type="text" placeholder=""/>
							<picker  class="form-suffix-space" mode="selector" disabled="true" :range="drugunitRange"  @change="setDoseRange1">
								<text class="suffix-text">{{dialysisInfo.first_drug_unit}}</text>
								<uni-icons type="arrowdown" color="#808080" size="20"></uni-icons>
							</picker>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>追加:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" disabled="true" :value="dialysisInfo.second_drug_dosage" type="text" placeholder=""/>
							<picker class="form-suffix-space" mode="selector" disabled="true" :range="drugunitRange"  @change="setDoseRange2">
								<text class="suffix-text">{{dialysisInfo.second_drug_unit}}</text>
								<uni-icons type="arrowdown" color="#808080" size="20"></uni-icons>
							</picker>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>钙mmol/L:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input type="text" :value="dialysisInfo.calcium_ion" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>血流量ml/min:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input type="text" :value="dialysisInfo.bloow_flow" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透析液流量ml/h:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input type="text" :value="dialysisInfo.dialysate_flow" placeholder=""/>
						</view>
					</view>
				</view>
				<view v-show="segCtrlSelected === 1">
					<view class="form-item">
						<view class="form-prefix-space">
							<text>内瘘震颤:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="beforeCondition" @change="setTremor">
								<label  v-for="item in info.tremor" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>杂音:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="beforeCondition"  @change="setNoise">
								<label  v-for="item in info.noise" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>				
					<view class="form-item">
						<view class="form-prefix-space">
							<text>红肿:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="beforeCondition" @change="setSwell">
								<label  v-for="item in info.swell" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>狭窄:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="beforeCondition" @change="setNarrow">
								<label  v-for="item in info.narrow" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>瘤样扩张:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="beforeCondition" @change="setExpand">
								<label  v-for="item in info.expand" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>护理措施:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<checkbox-group name="beforeCondition" @change="setmedicare">
								<label v-for="item in info.medicare" class="radio-item">
									<checkbox :value="item.value" :checked="item.checked" color="#51D3C7" style="transform:scale(0.7)" />
										<text>{{item.name}}</text>
								</label>
							</checkbox-group>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>实际治疗时间:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.actual_cleanup_hour" type="text" placeholder=""/>
							<text>h</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input type="text" :value="treatStateInfo.actual_cleanup_minute" placeholder=""/>
							<text>min</text>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>干体重Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.dry_weight" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>上次透后体重Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.last_time_dry_weight" type="text" placeholder=""/>
						</view>
					</view>				
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透前体重Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.before_dry_weight" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							//平车轮椅
							<text></text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="beforeCondition" @change="setExpand">
								<label  v-for="item in info.transfer" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>体重增加Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.dry_weight_tag" type="text" placeholder=""/>
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>较干体重增加Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.dry_weight_add" type="text" placeholder=""/>
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>预计脱水Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.dry_water_value" type="text" placeholder=""/>
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透后体重Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.after_dry_weight" type="text" placeholder=""/>
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>实际脱水Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.actualdisplacement_liquid" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>期望体重下降量Kg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.hope_weight_lose" type="text" placeholder=""/>
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透前血压mmHg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.before_systolic_pressure" type="text" placeholder=""/>
						</view>
						<text>/</text>
						<view class="form-cnt form-mid-space">
							<input type="text" :value="treatStateInfo.before_diastolic_pressure" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透后血压mmHg:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.after_systolic_pressure" type="text" placeholder=""/>
						</view>
						<text>/</text>
						<view class="form-cnt form-mid-space">
							<input type="text" :value="treatStateInfo.after_diastolic_pressure" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透后体温℃:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.after_temperature" type="text" placeholder=""/>
						</view>
					</view>				
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透后脉搏(次/min):</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.after_heart_rate" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透后呼吸(次/min):</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" :value="treatStateInfo.afterbr" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>置换液总量:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<input class="form-mid-space" value="" type="text" placeholder=""/>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>透析器凝血:</text>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<radio-group name="beforeCondition">
								<label  v-for="item in info.cruor" class="radio-item">
									<radio :value="item.value" :checked="item.checked" color="#51D3C7"  style="transform:scale(0.7)" />
									{{item.name}}
								</label>					
							</radio-group>	
						</view>
					</view>	
					<view class="form-item">
						<view class="form-prefix-space">
							<text>责任医生:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<picker mode="selector" :range="doctorRange"  @change="setDoctor">
								<input class="form-mid-space" :value="treatStateInfo.primary_doctor" type="text" placeholder=""/>
							</picker>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>责任护士:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<picker mode="selector" :disabled="nursePicker1" :range="nurseRange"  @change="setNurese1">
								<input class="form-mid-space" :value="treatStateInfo.primary_nurse" :disabled="nursePicker1" type="text" placeholder=""/>
							</picker>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<button :class="nursePicker1 ?'one-btn small-btn':'err-btn small-btn'" @click="ableNurse1">修改签名</button>
						</view>
					</view>										
					<view class="form-item">
						<view class="form-prefix-space">
							<text>核对护士:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<picker mode="selector" :disabled="nursePicker2" :range="nurseRange"  @change="setNurese2">
								<input class="form-mid-space" :value="treatStateInfo.check_nurse" :disabled="nursePicker2" type="text" placeholder=""/>
							</picker>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<button :class="nursePicker2 ?'one-btn small-btn':'err-btn small-btn'" @click="ableNurse2">修改签名</button>
						</view>
					</view>
					<view class="form-item">
						<view class="form-prefix-space">
							<text>穿刺护士:</text>
						</view>
						<view class="form-cnt form-mid-space">
							<picker mode="selector" :disabled="nursePicker3" :range="nurseRange"  @change="setNurese3">
								<input class="form-mid-space" :value="treatStateInfo.puncture_nurse" :disabled="nursePicker3" type="text" placeholder=""/>
							</picker>
						</view>
						<view class="form-cnt-noborder form-mid-space">
							<button :class="nursePicker3 ?'one-btn small-btn':'err-btn small-btn'" @click="ableNurse3">修改签名</button>
						</view>
					</view>	
					<button class="one-btn">保存</button>
				</view>
			</form>			
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				segCtrlItems:['透析信息', '评估与治疗情况'],
				segCtrlSelected:0,
				patient:{},
				date:"",
				statusInput:false,
				healHour:"",
				healMin:"",
				antiFreeze:"",
				treatmentRange:[],
				treatmentInfo:[],
				fliterRange:[],
				fliterInfo:[],
				bloodmethodRange:[],
				bloodmethodInfo:[],
				drugunitRange:[],
				drugunitInfo:[],
				doctorRange:[],
				doctorInfo:[],
				nurseRange:[],
				nurseInfo:[],
				nursePicker1:true,
				nursePicker2:true,
				nursePicker3:true,
				dialysisInfo:{},
				treatStateInfo:{},
				info:{
					status:[{name:'有', checked:false, value:'1'},{name:'无', checked:false, value:'0'},],
					puncture:[{name:'钝针', checked:false, value:'0'},{name:'锐针', checked:false, value:'1'}],
					direction:[{name:'向心', checked:false, value:'0'},{name:'离心', checked:false, value:'1'}],
					tremor:[{name:'强', checked:false, value:'0'},{name:'中', checked:false, value:'1'},{name:'弱', checked:false, value:'2'},{name:'无', checked:false, value:'3'},],
					noise:[{name:'强', checked:false, value:'0'},{name:'中', checked:false, value:'1'},{name:'弱', checked:false, value:'2'},{name:'无', checked:false, value:'3'},],
					swell:[{name:'有', checked:false, value:'0'},{name:'无',checked:false,value:'1'},],
					narrow:[{name:'有', checked:false, value:'0'},{name:'无', checked:false, value:'1'},],
					expand:[{name:'有', checked:false, value:'0'},{name:'无', checked:false, value:'1'},],
					medicare:[{name:'无', checked:true, value:'wu'},{name:'热敷', checked:false, value:'refu'},{name:'喜疗妥', checked:false, value:'xiliaotuo'},{name:'硫酸镁', checked:false, value:'liusuanmei'},],
					transfer:[{name:'平车', checked:false, value:'0'},{name:'轮椅', checked:false, value:'1'},],
					cruor:[{name:'0级', checked:false, value:'0'},{name:'1级', checked:false, value:'1'},{name:'2级', checked:false, value:'2'},{name:'3级', checked:false, value:'3'},],
				},
			}
		},
		onLoad() {
			// //获取护士列表
			// this.$myRequest({
			// 	url:'/common/nurselist',
			// 	success: (res) => {
			// 		console.log(res.data.data);
			// 		if(res.data.code == 200){
			// 			this.nurseInfo = res.data.data;
			// 			for (let i = 0; i < this.nurseInfo.length; i++) {
			// 				this.nurseRange.push(this.nurseInfo[i].name);
			// 			}
						
			// 			//获取核对护士名称
			// 			console.log("获取核对护士名称");
			// 			for (let i = 0; i < this.nurseInfo.length; i++) {
			// 				console.log("要找的那个护士",this.treatStateInfo.check_nurse);
			// 				console.log("核对护士id",this.nurseInfo[i].emp_no);
			// 				console.log("核对护士名称",this.nurseInfo[i].name);
			// 				console.log("序号",i);
			// 				if(this.nurseInfo[i].emp_no == this.treatStateInfo.check_nurse){
			// 					this.treatStateInfo.check_nurse = this.nurseInfo[i].name;
			// 					console.log("check_nurse",this.treatStateInfo.check_nurse);
			// 					break;
			// 				}
			// 			}
			// 		}
			// 		else{
			// 			uni.showToast({
			// 				title: '护士列表请求失败',
			// 				mask: true
			// 			});
			// 		}
			// 	},
			// });
		},
		onShow: function () {
			this.patient = uni.getStorageSync("patient");
			console.log(this.patient);
			this.patient.frequency_hours
			//在当前页从缓存中获取治疗方式
			this.treatmentInfo = uni.getStorageSync("treatmentInfo");
			for (let i = 0; i < this.treatmentInfo.length; i++) {
				this.treatmentRange.push(this.treatmentInfo[i].item_value);
			}
			//在当前页从缓存中获取净化器列表
			this.fliterInfo = uni.getStorageSync("fliterInfo");
			for (let i = 0; i < this.fliterInfo.length; i++) {
				this.fliterRange.push(this.fliterInfo[i].item_value);
			}
			//在当前页从缓存中获取抗凝方式列表
			this.bloodmethodInfo = uni.getStorageSync("bloodmethodInfo");
			for (let i = 0; i < this.bloodmethodInfo.length; i++) {
				this.bloodmethodRange.push(this.bloodmethodInfo[i].item_name);
			}
			console.log(this.bloodmethodInfo);
			//在当前页从缓存中获取药品单位列表
			this.drugunitInfo = uni.getStorageSync("drugunitInfo");
			for (let i = 0; i < this.drugunitInfo.length; i++) {
				this.drugunitRange.push(this.drugunitInfo[i].item_name);
			}
			//在当前页从缓存中获取医生列表和护士列表
			this.doctorInfo = uni.getStorageSync("doctorInfo");
			for (let i = 0; i < this.doctorInfo.length; i++) {
				this.doctorRange.push(this.doctorInfo[i].name);
			}
			this.nurseInfo = uni.getStorageSync("nurseInfo");
			for (let i = 0; i < this.nurseInfo.length; i++) {
				this.nurseRange.push(this.nurseInfo[i].name);
			}
			if(this.treatmentInfo == "error"){
				uni.showToast({
					title: '治疗方式请求失败',
					icon: 'none',
					mask: true
				});
			}
			if(this.fliterInfo == "error"){
				uni.showToast({
					title: '净化器列表请求失败',
					icon: 'none',
					mask: true
				});
			}
			if(this.bloodmethodInfo == "error"){
				uni.showToast({
					title: '抗凝方式列表请求失败',
					icon: 'none',
					mask: true
				});
			}
			if(this.drugunitInfo == "error"){
				uni.showToast({
					title: '药品单位列表请求失败',
					icon: 'none',
					mask: true
				});
			}
			if(this.nurseInfo == "error"){
				uni.showToast({
					title: '护士列表请求失败',
					icon: 'none',
					mask: true
				});
			}
			if(this.doctorInfo == "error"){
				uni.showToast({
					title: '医生列表请求失败',
					icon: 'none',
					mask: true
				});
			}
			//获取病人透析信息
			let searchInfo = uni.getStorageSync("searchInfo");
			this.date = searchInfo.date;
			this.$myRequest({
				url:'/patient/dialysisinfo',
				method:'POST',
				data:{
					patient_id:this.patient.patient_id,
					date:this.date,
				},
				success: (res) => {
					if(res.data.code == 200){
						console.log("透析信息:",res.data.data);
						this.dialysisInfo = res.data.data;
						this.patient.cure_id = this.dialysisInfo.cure_id;
						uni.setStorageSync("patient",this.patient);
						//页面加载信息：上机前病情
						for (let i = 0; i < this.info.status.length; i++) {
							if(this.info.status[i].value == this.dialysisInfo.has_special_state){
								this.info.status[i].checked = true;
								break;
							}
						}
						if(this.dialysisInfo.has_special_state == "1"){
							this.statusInput = true;
						}
						//页面加载信息：治疗时间
						if(this.dialysisInfo.frequency_hours == ""){
							this.healHour = "0";
							this.healMin = "0";
						}
						else{
							let tempTime = this.dialysisInfo.frequency_hours.split(".");
							this.healHour = tempTime[0];
							let min = "0."+ tempTime[1];
							min = parseInt(min)*60;
							this.healMin = min.toString();
						}
						//页面加载信息：穿刺
						for (let i = 0; i < this.info.puncture.length; i++) {
							if(this.info.puncture[i].value == this.dialysisInfo.chuanci_dunzhen){
								this.info.puncture[i].checked = true;
								break;
							}
						}
						//页面加载信息：方向
						for (let i = 0; i < this.info.direction.length; i++) {
							if(this.info.direction[i].value == this.dialysisInfo.chuanci_fangxiang){
								this.info.direction[i].checked = true;
								break;
							}
						}
						//页面加载信息：抗凝方式
						if(this.dialysisInfo.species != ""){
							for (let i = 0; i < this.bloodmethodInfo.length; i++) {
								if(this.dialysisInfo.species == this.bloodmethodInfo.item_id){
									this.antiFreeze = this.bloodmethodInfo.item_name;
									break;
								}
							}
						}
					}
				},
			});
			//获取病人评估与治疗情况
			this.$myRequest({
				url:'/patient/treatstate',
				method:'POST',
				data:{
					patient_id:this.patient.patient_id,
					date:this.date,
				},
				success: (res) => {
					if(res.data.code == 200){
						console.log("病人评估与治疗情况:",res.data.data);
						this.treatStateInfo = res.data.data;
						//页面加载信息：内瘘震颤
						for (let i = 0; i < this.info.tremor.length; i++) {
							if(this.info.tremor[i].value == this.treatStateInfo.in_basket_wound_allergy){
								this.info.tremor[i].checked = true;
								break;
							}
						}
						//页面加载信息：杂音
						for (let i = 0; i < this.info.noise.length; i++) {
							if(this.info.noise[i].value == this.treatStateInfo.in_basket_plaster_allergy){
								this.info.noise[i].checked = true;
								break;
							}
						}
						//页面加载信息：红肿
						for (let i = 0; i < this.info.swell.length; i++) {
							if(this.info.swell[i].value == this.treatStateInfo.in_basket_hongzhong){
								this.info.swell[i].checked = true;
								break;
							}
						}
						//页面加载信息：狭窄
						for (let i = 0; i < this.info.narrow.length; i++) {
							if(this.info.narrow[i].value == this.treatStateInfo.in_basket_xiazhai){
								this.info.narrow[i].checked = true;
								break;
							}
						}
						//页面加载信息：瘤样扩张
						for (let i = 0; i < this.info.expand.length; i++) {
							if(this.info.expand[i].value == this.treatStateInfo.in_basket_liuyangkuozhang){
								this.info.expand[i].checked = true;
								break;
							}
						}
						//页面加载信息：护理措施
						if(this.treatStateInfo.in_basket_huliliusuanmei == "" && this.treatStateInfo.in_basket_huliliusuanmei == "" && this.treatStateInfo.in_basket_huliliusuanmei == ""){
							
						}
						else{
							this.info.medicare[0].checked = false;
							if(this.treatStateInfo.in_basket_hulirefu == "1"){
								this.info.medicare[1].checked = true;
							}
							if(this.treatStateInfo.in_basket_hulixiliaotuo == "1"){
								this.info.medicare[2].checked = true;
							}
							if(this.treatStateInfo.in_basket_huliliusuanmei == "1"){
								this.info.medicare[3].checked = true;
							}
						}

						//页面加载信息：透析器凝血
						for (let i = 0; i < this.info.expand.length; i++) {
							if(this.info.cruor[i].value == this.treatStateInfo.coagulation_in_dialyser){
								this.info.cruor[i].checked = true;
								break;
							}
						}
						//获取核对医生名称
						for (let i = 0; i < this.doctorInfo.length; i++) {
							if(this.doctorInfo[i].emp_no == this.treatStateInfo.primary_doctor){
								this.treatStateInfo.primary_doctor = this.doctorInfo[i].name;
								break;
							}
						}						
						//获取核对护士名称
						for (let i = 0; i < this.nurseInfo.length; i++) {
							if(this.nurseInfo[i].emp_no == this.treatStateInfo.primary_nurse){
								this.treatStateInfo.primary_nurse = this.nurseInfo[i].name;
							}
							if(this.nurseInfo[i].emp_no == this.treatStateInfo.check_nurse){
								this.treatStateInfo.check_nurse = this.nurseInfo[i].name;
							}
							if(this.nurseInfo[i].emp_no == this.treatStateInfo.puncture_nurse){
								this.treatStateInfo.puncture_nurse = this.nurseInfo[i].name;
							}
						}
					}
				},
			});
		},
		methods: {
			//页面tab切换
			onClickItem(e) {
				if (this.segCtrlSelected !== e.currentIndex) {
					this.segCtrlSelected = e.currentIndex;
				}
			},
			//导航返回按钮对应方法
			toPatientList(){
				this.treatStateInfo = {};
				this.segCtrlSelected = 0;
				this.statusInput = false;
				this.info.status=[{name:'有', checked:false, value:'1'},{name:'无', checked:false, value:'0'},];
				this.info.puncture=[{name:'钝针', checked:false, value:'0'},{name:'锐针', checked:false, value:'1'}];
				this.info.direction=[{name:'向心', checked:false, value:'0'},{name:'离心', checked:false, value:'1'}];
				this.info.tremor=[{name:'强', checked:false, value:'0'},{name:'中', checked:false, value:'1'},{name:'弱', checked:false, value:'2'},{name:'无', checked:false, value:'3'},];
				this.info.noise=[{name:'强', checked:false, value:'0'},{name:'中', checked:false, value:'1'},{name:'弱', checked:false, value:'2'},{name:'无', checked:false, value:'3'},];
				this.info.swell=[{name:'有', checked:false, value:'0'},{name:'无',checked:false,value:'1'},];
				this.info.narrow=[{name:'有', checked:false, value:'0'},{name:'无', checked:false, value:'1'},];
				this.info.expand=[{name:'有', checked:false, value:'0'},{name:'无', checked:false, value:'1'},];
				this.info.medicare=[{name:'无', checked:true, value:'wu'},{name:'热敷', checked:false, value:'refu'},{name:'喜疗妥', checked:false, value:'xiliaotuo'},{name:'硫酸镁', checked:false, value:'liusuanmei'},];
				uni.navigateTo({
					url: "../patient-list/patient-list",
				});
			},
			//导航刷新按钮对应方法
			reflesh(){
				console.log("aaa");
			},
			//下拉框选择治疗方式
			setTreatment(e){
				this.dialysisInfo.treatmentitems = this.treatmentRange[e.detail.value];
			},
			//下拉框选择净化器
			setFliter(e){
				this.dialysisInfo.filter = this.fliterRange[e.detail.value];
			},
			//下拉框选择抗凝方式
			setBloodmethod(e){
				// this.dialysisInfo.
			},
			//下拉框选择首剂药品单位
			setDoseRange1(e){
				this.dialysisInfo.first_drug_unit = this.drugunitRange[e.detail.value];
			},
			//下拉框选择追加药品单位
			setDoseRange2(e){
				this.dialysisInfo.second_drug_unit = this.drugunitRange[e.detail.value];
			},
			//选择变更：内瘘震颤
			setTremor(e){
				this.treatStateInfo.in_basket_wound_allergy = e.detail.value;
			},
			//选择变更：杂音
			setNoise(e){
				this.treatStateInfo.in_basket_plaster_allergy = e.detail.value;
			},
			//选择变更：红肿
			setSwell(e){
				this.treatStateInfo.in_basket_hongzhong = e.detail.value;
			},
			//选择变更：狭窄
			setNarrow(e){
				this.treatStateInfo.in_basket_xiazhai = e.detail.value;
			},
			//选择变更：瘤样扩张
			setExpand(e){
				this.treatStateInfo.in_basket_liuyangkuozhang = e.detail.value;
			},
			//选择变更：护理措施
			setmedicare(e){
				console.log(e.detail.value);
			},
			//下拉框选择医生
			setDoctor(e){
				this.treatStateInfo.primary_doctor = this.doctorRange[e.detail.value];
			},
			//责任护士修改按钮,解除禁用
			ableNurse1(){
				this.nursePicker1 = !this.nursePicker1;
			},
			//核对护士修改按钮,解除禁用
			ableNurse2(){
				this.nursePicker2 = !this.nursePicker2;
			},
			//穿刺护士修改按钮,解除禁用
			ableNurse3(){
				this.nursePicker3 = !this.nursePicker3;
			},
			//下拉框选择责任护士
			setNurese1(e){
				this.treatStateInfo.primary_nurse = this.nurseRange[e.detail.value];
			},
			//下拉框选择核对护士
			setNurese2(e){
				this.treatStateInfo.check_nurse = this.nurseRange[e.detail.value];
			},
			//下拉框选择穿刺护士
			setNurese3(e){
				this.treatStateInfo.puncture_nurse = this.nurseRange[e.detail.value];
			}
		}
	}
</script>

